.component-tray {
  position:absolute;
  right:0;
  top:0;
  width:25%;
  height:100%;
  background:#f5f5f5;
  font-size:14px;
  .tray__content {
    padding:.75em 2em;
  }
  .breakdown {
    list-style:none;
    li,& {
      padding-left:0;
      margin-left:0;
    }
    li {
      padding-left:1em;
      display:flex;
      margin-bottom:.5em;
    }
    label {
      width:70%;
      flex:.7;
      padding-right:5%;
    }
  }
}